
.about-section {
  padding: 80px 20px;
  background: black;
  color: #fff;
  min-height: 100vh;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
}

.about-title {
  text-align: left;
  font-size: 2rem;
  margin-bottom: 40px;
  color: white;
  position: relative;
}

@media (min-width: 768px) {
  .about-section {
    padding: 100px 20px;
  }
  
  .about-title {
    font-size: 2.5rem;
    margin-bottom: 50px;
    text-align: center;
  }
}

.about-title:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -15px;
  width: 80px;
  height: 4px;
  background-color: #3b82f6;
}

@media (min-width: 768px) {
  .about-title:after {
    left: 50%;
    transform: translateX(-50%);
  }
}

.about-content {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
}

@media (min-width: 768px) {
  .about-content {
    flex-direction: row;
    gap: 50px;
    margin-bottom: 60px;
  }
}

.about-image-container {
  flex: 0 0 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

@media (min-width: 768px) {
  .about-image-container {
    flex: 0 0 30%;
    margin-bottom: 0;
  }
}

.about-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  border: 5px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

@media (min-width: 480px) {
  .about-image {
    width: 250px;
    height: 250px;
  }
}

@media (min-width: 768px) {
  .about-image {
    width: 300px;
    height: 300px;
    margin: 0 auto;
  }
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid #ccc;
  display: block;
}

.about-text {
  flex: 1;
  text-align: left;
}

.about-subtitle {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #e5e7eb;
  text-align: left;
}

.skills-subtitle {
  margin-top: 25px;
}

.about-text p {
  margin-bottom: 20px;
  line-height: 1.6;
  color: #d1d5db;
  text-align: left;
}

.about-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 20px;
}

.skill-tag {
  background-color: rgba(79, 70, 229, 0.2);
  color: #93c5fd;
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  border: 1px solid rgba(147, 197, 253, 0.3);
}

.about-extra {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

@media (min-width: 768px) {
  .about-extra {
    flex-direction: row;
    gap: 50px;
  }
}

.about-education, .about-experience {
  flex: 1;
}

.timeline {
  position: relative;
  margin-top: 30px;
  padding-left: 10px;
}

.timeline:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
}

.timeline-item {
  position: relative;
  padding-left: 30px;
  margin-bottom: 30px;
}

.timeline-item:before {
  content: '';
  position: absolute;
  left: -6px;
  top: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #3b82f6;
}

.timeline-date {
  font-weight: 600;
  color: #93c5fd;
  margin-bottom: 5px;
}

.timeline-content h4 {
  margin: 0 0 5px;
  font-size: 1.1rem;
  color: #f3f4f6;
}

.timeline-content p {
  margin: 0 0 10px;
  color: #d1d5db;
}

.timeline-content ul {
  padding-left: 20px;
  margin: 10px 0 0;
}

.timeline-content li {
  margin-bottom: 5px;
  color: #9ca3af;
}

